export const tradeStock = (stock, stock_data) => {
  return (dispatch, getState, {getFirestore}) => {
    const firestore = getFirestore();
    const profile = getState().firebase.profile;
    const traderId = getState().firebase.auth.uid;
    var stock_map = getState().firebase.profile.stocks;
    var liquid = getState().firebase.profile.liquid;
    const total = getState().firebase.profile.total;
    const cost = parseInt("" + stock_data[stock.name].price) * parseInt(stock.quantity + "");
    if(cost <= liquid) {
      liquid = liquid - cost;
      if (stock.name in stock_map) {
        stock_map[stock.name] = parseInt(stock_map[stock.name]) + parseInt(stock.quantity + "");
      }
      else {
        stock_map[stock.name] =  parseInt(stock.quantity + "");
      }
    
      firestore.collection('users').doc(traderId).update({
      stocks: stock_map,
      liquid: liquid,
      total: total
      }).then(() => {
        dispatch({ type: 'TRADE_SUCCESS' });
      }).catch(err => {
        dispatch({ type: 'TRADE_ERROR' }, err);
      });
    } else {
      dispatch({type: 'TRADE_ERROR'}, 'not that much liquid');
    }
  }
};