import React from 'react'
import { connect } from 'react-redux'
import { firestoreConnect } from 'react-redux-firebase'
import { compose } from 'redux'
import { Redirect } from 'react-router-dom'
import moment from 'moment'

const StockDetails = (props) => {
  const { auth, stock_info } = props;
  if (!auth.uid) return <Redirect to='/signin' /> 
  if (stock_info) {
    return (
      <div className="container section stock-details">
        <div className="card z-depth-0">
          <div className="card-content">
            <span className="card-title">{stock_info.name}</span>
            <span className="card-title">{stock_info.price}</span>
            <p>{stock_info.ticker}</p>s
          </div>
          <div className="card-action grey lighten-4 grey-text">
          </div>
        </div>
      </div>
    )
  } else {
    return (
      <div className="container center">
        <p>Loading Stock...</p>
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  // console.log(state);
  const name = ownProps.match.params.id;
  console.log(name);
  const stock_data = state.firestore.data.stock_data;
  const stock_info = stock_data ? stock_data[name] : null;


  return {
    stock_info: stock_info,
    auth: state.firebase.auth
  }
}

export default compose(
  connect(mapStateToProps),
  firestoreConnect([{
    collection: 'stock_data'
    
  }])
)(StockDetails)
